import React from "react"
import { Link } from "gatsby"
import json from "/src/siteData.json"

const Component = ({ jobExplanation, jobs, recruit = [] }) => {

  const jobPages = recruit.filter((page) => page.type === 102)

  {/* 1職種ずつ取得 */ }
  const uniqueJobTitles = [
    ...new Set(
      jobs.map((_, index) =>
        jobPages[index].context?.recruitment?.recruits?.find(
          (item) => item.title === "職種" || item.title === "職種名"
        ).content
      )
    )
  ]

  return (
    <section className="jobSec">
      <div className="wideContent">

        <div className="comTtlBox">
          <h2 className="headLine fadeInUp">JOB LIST</h2>
          {jobExplanation !== "コメントアウト" && (
            <p className="title fadeInUp" dangerouslySetInnerHTML={{ __html: jobExplanation }} />
          )}
        </div>

        <div className="infoSec">

          {/* 1職種ずつ繰り返し */}
          {uniqueJobTitles.map((jobTitle, i) => {
            const relatedJobs = jobs
              .map((jobContext, index) => ({
                jobContext,
                index,
                title: jobPages[index].context?.recruitment?.recruits?.find(
                  (item) => item.title === "職種" || item.title === "職種名"
                ).content
              }))
              .filter(item => item.title === jobTitle);

            const { jobContext, index } = relatedJobs[0];
            const jobMedia = jobContext.media;
            const workMedia = jobPages[index].context?.work?.medias?.[0]?.url;

            return (
              <div key={i} className="innerBox fadeInUp">
                {/* KV表示 */}
                {jobMedia.type === 1 ? (
                  <div className="photoBox" style={{ backgroundImage: `url(${jobMedia.url})` }}><h3>{jobTitle}</h3></div>
                ) : (
                  <div className="photoBox" style={{ backgroundImage: `url(${workMedia})` }}><h3>{jobTitle}</h3></div>
                )}

                <section className="textBox">

                  {/* エリアごとのボタン */}
                  <div className="comMoreWrap">
                    {relatedJobs.map(({ jobContext, index }, idx) => {
                      const area = jobPages[index].context?.recruitment?.recruits?.find(item => item.title === "エリア")?.content;
                      return (
                        <div className="comMore" key={idx}>
                          <Link title={jobContext.title} to={`/${jobContext.slug}`}>{area} × {jobContext.title.includes("正社員") && "正社員"}
                            {jobContext.title.includes("アルバイト・パート") && "アルバイト・パート"}
                            {jobContext.title.includes("業務委託") && "業務委託"}
                            {jobContext.title.includes("契約社員") && "契約社員"}</Link>
                        </div>
                      );
                    })}
                  </div>
                </section>
              </div>
            );
          })}
        </div>
      </div>
    </section>
  )
}

export default Component
